<template>
    <b-row class="m-0 p-0">
        <image-greeting-component :greeting="$ml.get('news')"/>
        <newscards-component class="mt-5" :news="news"/>
    </b-row>
</template>

<script>
    import NewscardsComponent from '../components/NewscardsComponent';
    import ImageGreetingComponent from '../components/ImageGreetingComponent';
    export default {
        components: {
            NewscardsComponent,
            ImageGreetingComponent
        },
        data() {
            {
                return {
                    news: []
                }
            }
        },
        beforeMount() {
            this.$axios.get('/aktuelles')
                .then((response) => {
                    const data = response.data.aktuelles;
                    // read created at property and add a new property that contains the Date object of it
                    data.forEach((entry) => {
                        entry.created_at_date = new Date(entry.created_at.replace(/-/g, '/'));
                    });

                    // sort data by descending Date
                    const sortedData = data.sort((a,b) => {
                        return new Date(b.created_at_date) - new Date(a.created_at_date);
                    });

                    this.news = sortedData;
                })
                .catch((error) => {
                    console.log(error.error);
                })
        }
    }
</script>

